import "../public/css/dataexport.css"
const $ = require("jquery")
// localStorage.setItem("token","1e22a5106c83414bbc2df853d9a4335b")
let startIndex = 10
const param = {
    pageIndex: 1,
    pageSize: 10,
    startIndex: 1
}
async function getData(data, token, startIndex) {
    if($("#qd").val() !== ""){
       data.channelGuid = $("#qd").val()
    }
    console.log(data)
    let promiseArr = []
    return await fetch('https://rental.hellobike.com/partner/driver/queryAllDriverInfo', {
        method: 'POST',
        body: JSON.stringify(data),
        headers: {
            token: token,
            "Content-Type": "application/json;charset=UTF-8"
        }
    }).then(data => {
        return data.json()
    }).then(data => {
        let resultAll = data?.data?.allDriverInfo
        for (let i = 0; i < resultAll.length; i++) {
            let user = resultAll[i]
            if (user && user.guid) {
                const url = `https://rental.hellobike.com/partner/driverSubmit/queryByGuid?guid=${user.guid}`
                let pro = fetch(url, {
                    headers: {
                        token: token
                    }
                }).then(data => {
                    return data.json()
                }).then(data => {
                    let certificationResult = data.data.driverCertificationInfo
                    let driverInfo = data.data.driverInfo
                    let registerStatus = certificationResult.registerStatus,  //认证成功
                        driverOnlineStatus = certificationResult.driverOnlineStatus,  //审核通过
                        transportOnlineStatus = certificationResult.transportOnlineStatus, // 审核通过
                        registerRejectReason = certificationResult.registerRejectReason,
                        info = user.name + user.phone + data.data.driverInfo.vehiclePlateNum
                    if (driverInfo.vehiclePlateNum == "null" || !driverInfo.vehiclePlateNum) {
                        info = user.name + user.phone
                    }
                    if (registerRejectReason == "null" || !registerRejectReason) {
                        registerRejectReason = registerStatus
                    }
                    let resultArr = [i + startIndex, user.channelName, info, user.createTime]
                    if (registerStatus === '认证成功') {
                        if (driverOnlineStatus === "审核通过" && transportOnlineStatus === "审核通过") {
                            resultArr.push("通过")
                        } else if (driverOnlineStatus === "审核通过" || transportOnlineStatus === "审核通过") {
                            resultArr.push("单证")
                        } else {
                            resultArr.push("缺双证")
                        }
                    } else {
                        resultArr.push(registerRejectReason)
                    }
                    return resultArr
                })
                promiseArr.push(pro)

            }
        }
        return promiseArr
    })
}

async function returnPromiseArr() {
    let dataPromiseArr = []
    for (let i = startIndex - 9; i <= startIndex; i++) {
        param.pageIndex = i
        param.startIndex = i
        dataPromiseArr.push(await getData(param, sessionStorage.getItem("token"), 1 + (i - 1) * param.pageSize))//
    }
    return dataPromiseArr
}
$("#getdata").on('click', () => {
    $(".wrapper.show").addClass('hide')
    $('tbody').html("")
    returnPromiseArr().then(data => {
        Promise.all(data.flat()).then(data => { 
            data.sort((a, b) => a[0] - b[0]).filter(data=> data[3].includes($("#sj").val())).forEach((prop, index) => {
                $('tbody').append($(`<tr><td data-code='xh'>${index + 1}</td><td data-code='qd'>${prop[1]}</td><td data-code='yh'>${prop[2]}</td><td data-code='sj'>${prop[3]}</td><td data-code='xx'>${prop[4]}</td></tr>`))
            })
            $(".wrapper.show").removeClass('hide')
        })
    })
})

function changeData() {
    param.pageSize = $('#pagesize').val()
    startIndex = $('#pagenum').val()
}

function screen(target, origin) {
    return origin.includes(target)
}

$('.input-data').on('change', () => {
    changeData()
})

$(".copy").on('click', () => {
    let dataHtml = $("#myTable")[0].outerHTML
    let blodHtml = new Blob([dataHtml], { type: 'text/html' })
    let data = [new ClipboardItem({
        [blodHtml.type]: blodHtml
    })]
    navigator.clipboard.write(data).then(data => {
        alert("复制成功")
    }, data => {
        console.log(data)
    })
})

$(".export").on('click', () => {
    let dataHtml = $("#myTable")[0].outerHTML
    let blodHtml = new Blob([dataHtml], { type: 'text/plain;charset=utf-8' })
    blodHtml = new Blob([String.fromCharCode(0xFEFF), blodHtml], { type: blodHtml.type });
    let url = window.URL.createObjectURL(blodHtml)
    let a = $(`<a href="${url}"></a>`)[0]
    a.download = "test.xls"
    $("body").append($(a))
    a.click()
    $("body").remove(a)
})

$(".sj .del").on('click',()=>{
    $("#sj").val("")
})
fetch("https://rental.hellobike.com/partner/qrCode/getQrCodeList", {
    method: "GET",
    headers: {
        token: sessionStorage.getItem("token"),
        "Content-Type": "application/json;charset=UTF-8"
    }
}).then(data => {
    return data.json()
}).then(data => {
    for (const prop of data.data) {
        $("#qd").append($(`<option value='${prop.guid}' class='text-dark'>${prop.channelName}</option>`))
    }
})
